<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 为用户添加ExBot过期时间
            v-container
                v-row 
                    v-col(cols="12" sm="12" lg="12")
                        v-text-field(outlined hide-details v-model="username"  label="用户名")
                    v-col(cols="12" sm="12" lg="12")
                        v-text-field(outlined hide-details v-model="expiredate" disabled  label="当前过期时间")
                    v-col(cols="12" sm="12" lg="12")
                        v-text-field(outlined hide-details v-model="days"  label="添加天数")

            v-card-actions
                v-row 
                    v-col(cols="12" sm="6" lg="6")
                        v-btn(color="success" dark @click="refresh") 刷新
                    v-col(cols="12" sm="6" lg="6")
                        v-btn(color="success" dark @click="adduserexpiretime") 添加
</template>


<script>
import * as api from "@/apis/wswebapi"
export default {
    data: () => ({
    username: "",
    expiredate: "",
    days:7,
    types:[{text:"ODKENCODE",value:"ODKENCODE"},
    {text:"ODKENCRYPTE",value:"ODKENCRYPTE"},
    ],
    type:{text:"ODKENCRYPTE",value:"ODKENCRYPTE"},
    }),
    methods:{
        getDateStr(timestamp){
            let date = new Date((Number(timestamp)+3600*8)*1000);
            return date.toISOString().substr(0, 19)
        },
        refresh(){
            api.GetUserExBotExpireTime({username:this.username}).then(res=>{
                if (res.code==200){
                    let date = new Date((Number(res.data)+3600*8)*1000);
                    this.expiredate=date.toISOString().substr(0, 19);
                    this.$store.dispatch('msglog/push_msg',{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch('msglog/push_msg',{message:res.msg+res.error,type:"error"})
                }
            })
        },

        adduserexpiretime(){
            api.AddUserExBotExpireTime({username:this.username,days:Number(this.days)}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch('msglog/push_msg',{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch('msglog/push_msg',{message:res.msg+res.error,type:"error"})
                }
            })
        }
    },
}
</script>


